#menu-toggle {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 20px;
  cursor: pointer;
}
#menu-toggle .toggle-top,
#menu-toggle .toggle-center,
#menu-toggle .toggle-bottom {
  width: 25px;
  height: 1px;
  padding: 0px;
  transition: 0.5s all ease-in-out;
}
#menu-toggle .toggle-top {
  margin-bottom: 5px;
}
#menu-toggle .toggle-center {
  margin-bottom: 5px;
}
#menu-toggle .toggle-bottom {
  margin-bottom: -0.2px;
}

#menu-toggle.active .toggle-top {
  transform: rotate(45deg);
  transition: 0.5s all ease-in-out;
  margin: 0px;
}
#menu-toggle.active .toggle-center {
  display: none;
}
#menu-toggle.active .toggle-bottom {
  transform: rotate(-45deg);
  margin: 0px;
  transition: 0.5s all ease-in-out;
}

.about {
  height: 100%;
  width: 100%;
  z-index: 999999;
  position: fixed;
}
.about__wrapper {
  display: flex;
  height: inherit;
  width: inherit;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.about__wrapper h2 {
  width: 420px;
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
}

.about.unactive {
  margin-left: -10000px;
  transition: 0.5s all ease-in-out;
}

.about.active {
  margin-left: 0px;
  transition: 0.5s all ease-in-out;
}

#about1st {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 9999999;
  position: fixed;
  padding: 2% 0;
  margin-top: -700px;
}
#about1st__wrapper {
  display: flex;
  height: inherit;
  width: inherit;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
#about1st__wrapper h2 {
  width: 420px;
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
}

#about1st.unactive {
  margin-left: -10000px;
  transition: 0.5s all ease-in-out;
}

#about1st.active {
  margin-left: -8px;
  transition: 0.5s all ease-in-out;
}

body.no-scroll {
  height: 100%;
  overflow-y: hidden;
}

div.theme-toggle {
  width: 20px;
  height: 20px;
  display: inline-flex;
}
div.theme-toggle__left {
  width: 8px;
  height: 18px;
  border-radius: 12px 0 0 12px;
  background-color: #fff;
}
div.theme-toggle__right {
  width: 8px;
  height: 18px;
  border-radius: 0 12px 12px 0;
  background-color: #000;
}

