main .change-page1__right .arrow-right-wrapper span.arrow::after {
  content: "";
  display: block;
  position: relative;
  width: 2px;
  height: 25px;
  transform: rotate(-45deg) translate(15px, 4px);
  left: -5px;
  top: 5px;
}
main .change-page1__left .arrow-left-wrapper, main .change-page1__right .arrow-left-wrapper {
  transform: rotate(-45deg);
  margin-left: -15px;
}
main .change-page1__left .arrow-right-wrapper, main .change-page1__right .arrow-right-wrapper {
  transform: rotate(135deg);
  margin-left: 10px;
}
main .change-page1__left span, main .change-page1__right span {
  width: 12.5rem;
  color: var(--light-bg-color);
  text-align: center;
  margin-bottom: 20px;
}
main .change-page1__right .surface {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
main .change-page1__right .behind {
  width: 16rem;
  height: 16rem;
  margin-top: 0px;
  margin-left: -10000px;
}
main .change-page1__right:hover .behind {
  margin-left: 0px;
}

